@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
.ContactForm_container__1INJV {
  position: relative;
}
.ContactForm_errorModal__MTR8t {
  background-color: red;
  width: 450px;
  padding: 15px;
  color: white;
  position: absolute;
  text-align: center;
  top: 0;
  right: 5px;
}

.errorModal-enter {
  transform: translateX(100%);
}
.errorModal-enter-active {
  transform: translateX(0%);
  transition: transform 250ms linear;
}
.errorModal-exit {
  transform: translateX(0%);
}
.errorModal-exit-active {
  transform: translateX(100%);
  transition: transform 250ms linear;
}

.ContactListItem_container__2jeJP {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  width: 450px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  padding: 0 10px;
  margin-bottom: 2px;
}
.ContactListItem_removeBtn__hDzns {
  width: 30px;
  height: 30px;
  background-color: red;
  border: none;
  border-radius: 4px;
  position: relative;
  margin-left: 10px;
}

.ContactListItem_removeBtn__hDzns:before,
.ContactListItem_removeBtn__hDzns:after {
  position: absolute;
  content: " ";
  height: 28px;
  width: 2px;
  background-color: #fff;
}
.ContactListItem_removeBtn__hDzns:before {
  transform: rotate(45deg);
  left: 13px;
  top: 1px;
}
.ContactListItem_removeBtn__hDzns:after {
  transform: rotate(-45deg);
  top: 1px;
  left: 15px;
}
.ContactListItem_smallContainer__1CbM9 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 200px;
}

.ContactList {
  padding: 0;
}
.ContactList-appear {
  transform: translateX(0%);
}
.ContactList-appear-active {
  transform: translateX(0%);
  transition: transform 250ms linear;
}
.contactListItem-enter {
  transform: translateX(-100%);
}
.contactListItem-enter-active {
  transform: translateX(0%);
  transition: transform 250ms linear;
}
.contactListItem-exit {
  transform: translateX(0%);
}
.contactListItem-exit-active {
  transform: translateX(-100%);
  transition: transform 250ms linear;
}

.App_container__6j1zw {
  font-family: "Montserrat", sans-serif;
  padding-left: 20px;
}
.App_logo__3qVxm {
  font-weight: 700;
  color: darkslategrey;
}

.logo-appear {
  transform: translateX(-100%);
}
.logo-appear-active {
  transform: translateX(0%);
  transition: transform 500ms linear;
}
